.main-nav {
  min-height: 200px;
  width: 100%;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 20px;
  text-transform: uppercase;
  line-height: 20px;
}
.main-nav__item {
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
.main-nav__link {
  color: #111111;
  font-weight: 500;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
  justify-content: center;
}
@media (max-width: 767px) {
  .main-nav--hidden {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    min-height: 82px;
    width: 441px;
  }
  .main-nav__list {
    display: flex;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link {
    font-weight: 400;
    padding-top: 41px;
  }
  .main-nav__link[href]:hover {
    opacity: 0.6;
  }
  .main-nav__link[href]:active {
    opacity: 0.3;
  }
  .main-nav__item {
    margin-right: 36px;
    border: none;
  }
  .main-nav__item:last-child {
    margin-right: 0;
    border: none;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    bottom: 14px;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1300px) {
  .main-nav {
    width: 490px;
    z-index: 2;
    margin-right: -5px;
  }
  .main-nav__list {
    margin-top: 32px;
    padding-left: 7px;
  }
  .main-nav__link {
    color: #ffffff;
    font-size: 20px;
  }
  .main-nav__link--inner {
    color: #111111;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    bottom: 14px;
    height: 2px;
    background-color: #ffffff;
  }
  .main-nav__item--active-inner {
    position: relative;
  }
  .main-nav__item--active-inner::after {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    bottom: 14px;
    height: 2px;
    background-color: #68b738;
  }
}
